<template>
  <div>
    <select-dept :visible.sync="visible" :loadData="getTree" :replaceFields="{ children: 'childrens', key: 'departmentId' }"></select-dept>
  </div>
</template>

<script>
import { SelectDept } from '@handday/components'
import { getTree } from '../../api/organization'
export default {
  name: 'SelectDepts',
  components: {
    SelectDept
  },
  data () {
    return {
      visible: false,
      getTree
    }
  },
  mounted() {
    setTimeout(() => {
      this.visible = true
    }, 1000)
  }
}
</script>

